.deposit {
  color: white;
  min-width: 666px;

  &__content {
    width: 600px;
    margin: 0 auto;
    padding: 24px 0;
  }
  &__title {
    font-family: 'Montserrat';
    font-weight: 600;
    font-size: 36px;
    color: #ffffff;
  }
  &__transfer {
    display: flex;
    align-items: center;
  }
  &__asset {
    margin-top: 81px;
    margin-bottom: 24px;
  }

  .transfer {
    flex: 1;
    &__icon {
      min-width: 50px;
      position: relative;
    }

    &__label {
      font-weight: 300;
      color: rgba($color: #fff, $alpha: 0.7);
      margin-bottom: 12px;
    }
    &__item {
      display: flex;
      background: #182031;
      border-radius: 4px;
      align-items: center;
      min-width: 200px;
      padding: 2px 16px;
      min-height: 60px;
    }
    &__button {
      text-align: center;
      padding-top: 50px;
    }
  }
  .button-next {
    padding: 10px 100px;
    background: #2156e1;
    border-radius: 30px;
    display: inline-block;
    cursor: pointer;
  }
  .info {
    &__name {
      font-weight: 600;
    }
    &__address {
      font-weight: 300;
      color: rgba($color: #fff, $alpha: 0.7);
      line-height: 20px;
    }
  }
  .icon-arrow {
    background: url('../../assets/images/icon-transfer-to.png') center/ 100%
      100% no-repeat;
    width: 18px;
    height: 14px;
    position: absolute;
    top: 8px;
    left: 50%;
    transform: translateX(-50%);
  }
  .icon-app {
    background: url('../../assets/images/banner_logo.png') center/100% 100%
      no-repeat;
    height: 40px;
    width: 100px;
  }
  .metamask {
    background: url('../../assets/images/MetaMask_Fox\ 1.png') center/100% 100%
      no-repeat;
    width: 40px;
    height: 40px;
    margin-right: 12px;
  }
  .deposit-input {
    background: #182031;
    border-color: #182031 !important;
    box-shadow: none !important;
    outline: none !important;
  }
  .max {
    background: #182031;
    border-color: #182031 !important;
    color: #ffd911;
    line-height: 46px;
    padding: 0 20px;
    cursor: pointer;
  }
  .inputgroup {
    display: flex;
    .deposit-input {
      flex: 1;
    }
  }
  .asset-dropdown {
    width: 100%;
    background: #182031;
    border-color: #182031 !important;
    box-shadow: none !important;
    outline: none !important;

    &:hover {
      border-color: #182031;
    }
  }
  .p-dropdown {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
  .deposit-input {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
  h6 {
    color: rgba($color: #fff, $alpha: 0.7);
    font-weight: 300;
  }
  .p-inputtext {
    min-height: 46px;
  }
  .p-dropdown-label {
    line-height: 30px;
  }
  &__amount {
    width: 100%;
  }
  .amount-label {
    margin-bottom: 0px;
  }

  .amount-total {
    text-align: right;
    color: white;
    font-weight: normal;
    font-size: 12px;
    margin-top: 0px;
    margin-bottom: 4px;
  }
  .p-dialog-header {
    background-color: #262836;
  }
  .p-dialog-content {
    background-color: #262836;
  }
}
