.inventory-card {
    display: flex;
    background: #292B38;
    border: 1px solid #393D49;  
    border-radius: 8px; 
    padding: 46px 56px;
    width: fit-content;
    gap: 24px;

    &__group {
        font-family: 'Teko-Regular';
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 34px;
        text-align: center;
        color: #FFFFFF;
        display: flex;
        flex-direction: column;
    }
}

.inventory {
    &__container {
        display: flex;
        flex-direction: column;
        max-width: 1064px;
        gap: 47px;
        margin-bottom: 31px;
        
        .line-1 {
            display: flex;
            flex-direction: row;
            justify-content: space-evenly;
        }

        .line-2 {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        }
    }
}

.inventory-page {
    width: 100%;
    padding-right: 36px;

    &__cards {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        column-gap: 36px;
    }
    .inventory-tab-card {
        color: #fff;
        background: #292B38;

        border-radius: 8px;
        padding: 60px 0;
        width: 100%;

        display: flex;
        justify-content: center;
        align-items: center;
        gap: 16px;

        &__bnb-icon {
            background: url('../../assets/images/banner_logo.png') center/100% 100%
            no-repeat;

            width: 45px;
            height: 45px;
        }

        &__content {
            font-family: 'Teko';
            font-style: normal;
            font-weight: bold;
            font-size: 24px;
            line-height: 34px;
            color: #fff;

            .filter-header {
                width: 30%;
                .input-number-id {
                  background-color: transparent;
                  color: white;
                  width: 48%;
                }
                .custom-number-heroid::-webkit-inner-spin-button, .custom-number-heroid::-webkit-outer-spin-button {
                  -webkit-appearance: none;
                  margin: 0;
                }
                .custom-number-heroid[type=number]{
                  -moz-appearance: textfield;
                }
                .p-dropdown {
                  background-color: transparent;
                  width: 48%;
                  height: 40px;
                  align-items: center;
                  .p-inputtext {
                    color: white;
                    margin-left: 12px;
                  }
                  .p-placeholder {
                    color: #baafbc;
                  }
                }
              }
        }
    }

    &__tabs {
        margin-top: 83px;
        margin-bottom: 45px;
    }
    
}

.cus-tabview {
    list-style: none;
    display: flex;
    flex-direction: row;

    width: 100%;
    padding: 0;

    border-bottom: 1px solid #F3BA2F;

    &__panel {
        font-family: 'Teko';
        font-size: 24px;
        font-weight: 300px;
        line-height: 34px;

        text-align: center;

        padding: 8px 40px;

        color: #fff;

        cursor: pointer;

        &:hover {
            color: #F3BA2F;
        }
    }

    .active {
        font-weight: 600;
        
        color: #F3BA2F;
        background: #292B38;
    }
}