.title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    color: #FFFFFF;
    margin-bottom: 31px;
}
.wallet {
    display: flex;
    gap: 32px;
    margin-bottom: 31px;

    .wallet-card {
        background: #262836;
        box-shadow: 0px 2px 4px rgba(193, 207, 217, 0.2);
        border-radius: 7px;
        padding: 24px 40px;
        display: flex;
        justify-content: flex-start;
        font-family: Montserrat;
        font-style: normal;
        gap: 26px;
        position: relative;
    
        &__group {
            display: flex;
            gap: 8px;
            flex-direction: column;
        }
    
        &__name {
            font-weight: normal;
            font-size: 14px;
            line-height: 17px;
            color: #B0BAC5;
            margin-bottom: 8px;
        }
    
        &__content {
            color: #FFFFFF;
            font-weight: 600;
        }
    
        .large {
            font-size: 28px;
            line-height: 34px;
        }
    
        .small {
            font-size: 15px;
            line-height: 24px;
        }
        
        &__button-group {
            display: flex;
            gap: 36px;
        }

        &__chart {
            position: absolute;
            top: 20px;
            right: 0;
        }
    }

    .flex-column {
        flex-direction: column;
    }
    
    .flex-row {
        flex-direction: row;
        align-items: center
    }
    .more-padding {
        padding-right: 105px;
    }
}


