.button {
    background: #2156E1;
    border-radius: 30px;
    text-align: center;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
    padding: 10px 40px;
    min-width: 172px;
    border: none;
    cursor: pointer;

    &:hover {
        background: #1e4ec8;
    }
}