.sidebar {
    background: #262836;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 43px 36px;

    img {
        width: 93px;
        height: 93px;
        border-radius: 14px;
        object-fit: cover;
        object-position: center;
        margin-bottom: 16px;
    }

    &__info {
        font-family: Montserrat;
        font-style: normal;
        display: flex;
        flex-direction: column;
        align-items: center;

        &-welcome {
            font-weight: normal;
            font-size: 18px;
            line-height: 22px;
            color: #FFFFFF;
            text-align: center;
            margin-bottom: 4px;

            .strong {
                font-weight: 600;
            }
        }

        &-email {
            font-weight: normal;
            font-size: 14px;
            line-height: 17px;
            color: #9B94C1;
        }
    }

    &__item {
        width: 100%;
        border-radius: 8px;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        font-family: Montserrat;
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: #FFFFFF;
        gap: 16px;
        padding: 12px;
        cursor: pointer;

        &:hover {
            color: #E4AF2C;
        }
    }

    &__items-container {
        margin-top: 64px;
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 20px;
    }
    .active {
        background: #1F2022;
        color: #E4AF2C;
    }
}